import React from "react"
import { useUser } from "@clerk/clerk-react"
import { UserAvatar } from "@rp/react/web/common/base-components"
import { Box, Typography } from "@rp/react/web/mui/core"

const BriefConfigCardUser = () => {
  const { isLoaded, user } = useUser()
  return (
    <Box display="flex" alignItems="center" role="note" aria-label="brief config owner" mt="auto">
      <UserAvatar size={24} ownerName={user?.fullName ?? undefined} isLoading={!isLoaded} />
      <Typography sx={{ ml: 1, fontSize: "12px", opacity: 0.5 }}>You</Typography>
    </Box>
  )
}

export { BriefConfigCardUser }
